.events-section {
  padding-top: 41px;
  padding-left: 48px;
  padding-bottom: 72px;
  margin-bottom: 78px;

  background-color: #fffcfc;
}

.calendar__list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.calendar__item {
  display: flex;
  flex: row;
  justify-content: space-between;
  align-items: center;
  padding: 4px 18px;
}
.calendar__item:nth-child(odd) {
  background-color: #c4c4c4;
}

.calendar-button {
  border: none;
  background-color: transparent;
  cursor: pointer;
}

@media screen and (min-width: 320px) {
  .events-section__title {
    font-family: 'Roboto';
    font-weight: 400;
    font-size: 35px;
    line-height: 59px;

    margin-bottom: 25px;
  }

  .calendar__title {
    font-family: 'Roboto';
    font-weight: 500;
    font-size: 20px;
    line-height: 1.3;

    margin-bottom: 25px;
  }

  .calendar__icon {
    width: 30px;
    height: 30px;

    &:hover,
    &:focus {
      fill: #423f3f;
      border: 1px solid;
    }
  }

  .calendar__item-title {
    font-family: 'Roboto';
    font-weight: 500;
    font-size: 18px;
    line-height: 1.4;
  }

  .calendar__item-descr {
    font-family: 'Roboto';
    font-weight: 500;
    font-size: 14px;
    line-height: 1.5;
  }
}

@media screen and (min-width: 767px) {
  .events-section__title {
    font-size: 50px;
    line-height: 59px;

    margin-bottom: 32px;
  }

  .calendar__title {
    font-size: 24px;

    margin-bottom: 34px;
  }

  .calendar__icon {
    width: 42px;
    height: 36px;
  }

  .calendar__item-title {
    font-size: 20px;
  }

  .calendar__item-descr {
    font-size: 16px;
  }
}
