.news-body {
  background-color: #e5e5e5;
}

.news-section {
  min-height: 590px;
  background-color: #fef8f8;
  background-image: url("seminar-min.49c412c2.png");
  background-position: 100%;
  background-repeat: no-repeat;
  background-size: 768px;
  margin-top: 38px;
  margin-bottom: 70px;
}

.news-section__wrapper {
  flex-direction: row;
  display: flex;
}

.news-section__form {
  padding: 49px 38px;
}

.news-section__nav {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 120px;
  display: flex;
}

.news-section__nav-link {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.news-section__nav-icon {
  margin-bottom: 12px;
}

.news-section__nav-text {
  text-align: center;
  color: #000;
  font-family: Roboto;
  font-size: 16px;
  font-weight: 700;
  line-height: 19px;
}

.news-section__title-alumni {
  color: var(--main-color);
}

@media screen and (min-width: 320px) {
  .news-section__title {
    margin-bottom: 18px;
    font-family: Roboto;
    font-size: 40px;
    font-weight: 400;
    line-height: 59px;
  }

  .news-section__text {
    margin-bottom: 20px;
    font-family: Roboto;
    font-size: 14px;
    font-weight: 400;
    line-height: 19px;
  }

  .news-section__nav-item {
    background-color: rgba(213, 213, 213, .808);
    border-radius: 15px;
    padding: 5px 7px;
  }

  .news-section__nav-item:not(:last-child) {
    margin-right: 10px;
  }

  .news-section__text {
    background-color: rgba(213, 213, 213, .808);
    border-radius: 15px;
    padding: 5px 7px;
  }
}

@media screen and (min-width: 1400px) {
  .news-section__title {
    margin-bottom: 22px;
    font-size: 50px;
    line-height: 59px;
  }

  .news-section__text {
    margin-bottom: 39px;
    font-size: 16px;
    line-height: 19px;
  }

  .news-section__nav-item, .news-section__text {
    background-color: rgba(0, 0, 0, 0);
  }
}

.events-section {
  background-color: #fffcfc;
  margin-bottom: 78px;
  padding-top: 41px;
  padding-bottom: 72px;
  padding-left: 48px;
}

.calendar__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.calendar__item {
  flex: row;
  justify-content: space-between;
  align-items: center;
  padding: 4px 18px;
  display: flex;
}

.calendar__item:nth-child(2n+1) {
  background-color: #c4c4c4;
}

.calendar-button {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
}

@media screen and (min-width: 320px) {
  .events-section__title {
    margin-bottom: 25px;
    font-family: Roboto;
    font-size: 35px;
    font-weight: 400;
    line-height: 59px;
  }

  .calendar__title {
    margin-bottom: 25px;
    font-family: Roboto;
    font-size: 20px;
    font-weight: 500;
    line-height: 1.3;
  }

  .calendar__icon {
    width: 30px;
    height: 30px;
  }

  .calendar__icon:hover, .calendar__icon:focus {
    fill: #423f3f;
    border: 1px solid;
  }

  .calendar__item-title {
    font-family: Roboto;
    font-size: 18px;
    font-weight: 500;
    line-height: 1.4;
  }

  .calendar__item-descr {
    font-family: Roboto;
    font-size: 14px;
    font-weight: 500;
    line-height: 1.5;
  }
}

@media screen and (min-width: 767px) {
  .events-section__title {
    margin-bottom: 32px;
    font-size: 50px;
    line-height: 59px;
  }

  .calendar__title {
    margin-bottom: 34px;
    font-size: 24px;
  }

  .calendar__icon {
    width: 42px;
    height: 36px;
  }

  .calendar__item-title {
    font-size: 20px;
  }

  .calendar__item-descr {
    font-size: 16px;
  }
}

